.App {
  color: #d6a838;
  font-weight: 200;
}

.bg {
  position: fixed;
  height: 100vh;
  width: 100%;
  left: 0;
  top: 0;
  overflow: hidden;
  z-index: -1; }

  .bg .dot,
  .bg .line {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    background-repeat: repeat;
    background-position: center;
    background-size: 1000px; }

  .bg .dot {
    height: 800%;
    /* background-image: url("../img/main/bg_dot.png"); */
    background-image: url("assets/img/bg_dot.png"); }

  .bg .line {
    height: 200%;
    /* background-image: url("../img/main/bg_line.png"); */
    background-image: url("assets/img/bg_line.png"); }

@media (min-width: 992px) {
  .container {
    max-width: 50rem !important;
  }
}

button {
  cursor: pointer;
}

.font-condensed {
  font-family: 'Open Sans Condensed', sans-serif;
}

.clickable {
  cursor: pointer !important;
}

.text-underline {
  text-decoration: underline !important;
}

.a {
  color: #007bff;
  text-decoration: none;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
  cursor: pointer;
}

.a:hover {
  color: #0056b3;
  text-decoration: underline;
}

.solid-rectangle-button {
  border: none;
  padding: 0.5rem 1rem;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.5);
}

.bg-golden {
  background-color: #d6a838;
  color: white;
}

.bg-trust-wallet-blue {
  background-color: #2C6AB3;
  color: white;
}

.trust-wallet-icon {
  height: 2.25em;
  width: 2.25em;
  border-radius: 0.625em;
}

.balance-container {
  max-width: 320px;
  width: 100%;
  margin: 0 auto;
}

@media screen and (min-width: 768px){
  .balance-icon-container {
    margin-right: 1rem;
  }
  .balance-text-container {
  }
}

@media screen and (max-width: 767px){
  .balance-icon-container {
    float: left;
  }
  .balance-icon-container img {
    vertical-align: top;
  }
  .balance-text-container {
    float: right;
  }
}

.narrow-container {
  max-width: 720px;
  margin-left: auto;
  margin-right: auto;
}

.copied-msg {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -125%);
  font-size: 0.875rem;
  border-radius: 0.25rem;

  padding: 0.25rem 0.5rem;
  background-color: #333;
  color: white;
  z-index: 1000;
}

.word-break-all {
  word-wrap: break-word;
  overflow-wrap: break-word;
  word-break: break-all;
}

.word-wrap-break-word {
  word-wrap: break-word;
  overflow-wrap: break-word;
}

.app-title {
  font-size: 1.25rem;
  font-weight: bold;
}

.app-navbar {
  position: sticky;
  top: 0;
  line-height: 3rem;
  height: 3rem;
  background-color: rgba(0, 0, 0, 0.15);
  padding-left: 1rem;
  padding-right: 1rem;
  box-sizing: border-box;
}

.tab-list {
  border-bottom: 1px #d6a838 solid;
  font-size: 1.25rem;
  text-align: center;
}

.tab-button {
  padding: 0.5rem 1rem;
  display: inline-block;
  text-align: center;
}

.tab-button-inactive {
  background-color: transparent;
  color: #d6a838;
}

.tab-button-active {
  background-color: #d6a838;
  color: white;
  font-weight: bold;
}
